.App {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.top {
  padding: 1rem;
  flex: 0;
  border-bottom: solid 2px darkgray;
}

.bottom {
  padding: 1rem;
  flex: 1;
}

.bottom .header h2 {
  margin: 0 0 0.5rem 0;
}

.header {
  display: flex;
  gap: 10px;
  align-items: center;
  margin-bottom: 1rem;
}

.header > * {
  margin: 0;
}

.toolbar {
  margin-left: auto;
  display: flex;
  align-items: center;
  gap: 10px;
}

.toolbar > button {
  padding: 0.5rem;
  font-size: 1rem;
}

.toolbar > button:not([disabled]) {
  cursor: pointer;
}

.flex-row {
  display: flex;
  flex-direction: row;
  gap: 5rem;
  justify-content: space-between;
}

.help {
  border: solid 2px darkgray;
  background-color: lightcyan;
  padding: 2rem;
}

.site-id {
  font-size: 1.25rem;
  width: 25rem;
  padding: 5px 15px;
}

.left {
  flex: 0 0 35%;
}

.right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

calcite-pick-list-item {
  --calcite-font-size--1: 2rem;
}

calcite-pick-list-item.published {
  --calcite-font-weight-normal: bold;
  --calcite-ui-text-1: purple;
}

.preview {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.section {
  display: flex;
  flex-direction: column;
  gap: 5px;
  border: solid 1px #bbb;
  padding: 5px;
}

.row {
  display: flex;
  flex-direction: row;
  gap: 5px;
  border: solid 1px #ccc;
  padding: 5px;
}

/* half assed grid... */

.row {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(12, 1fr);
}

[class*="card-"] {
  background-color: lightsteelblue;
  min-height: 40px;
  color: white;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
}

.version {
  background-color: lightcoral;
}

.card-3 {
  grid-column: auto / span 3;
}

.card-4 {
  grid-column: auto / span 4;
}

.card-6 {
  grid-column: auto / span 6;
}

.card-8 {
  grid-column: auto / span 8;
}

.card-9 {
  grid-column: auto / span 9;
}

.card-12 {
  grid-column: auto / span 12;
}
